/* CAROUSEL */

.carousel {
    background-color: rgb(25, 26, 26);
}

.slide-item {
    max-height: 30rem;
    width: 100%;
    overflow: hidden;
    display: inline-block;
    text-align: center;
    position: relative;
}

.slide-item::after {
    content:'';
    position:absolute;
    left:0; top:0;
    width:100%; height:100%;
    display:inline-block;
    background: linear-gradient(180deg, transparent 50%, rgb(25, 26, 26) 100%);
}
.slide-item .desc {
    color: rgb(230,230,230);
    position: absolute;
    top: 2rem;
    left: 2rem;
    right: 2rem;
}

